@mixin flexRow {
	display: flex;
	flex-direction: row;
	flex-wrap: nowrap;
}

@mixin flexColumn {
	display: flex;
	flex-direction: column;
}

@mixin dropShadow {
	text-shadow: 2px 2px 2px black;
}